import React from 'react';
import styled from 'styled-components';
import { createImage, createHoveredImage } from 'utils/imageUtil';
import { moveSlowly } from './Disconnected';

const Container = styled.div`
  position: relative;

  .title {
    left: 0;
    margin-block-start: 0;
    margin-block-end: 0;
    text-align: center;
    z-index: 2;
    font-size: 10vw;
    top: 0vh;
  }

  .label {
    top: 0vh;
  }

  .first-left-image {
    margin-top: 14vh;
  }
`;

const Time = () => {
  const titleRef = React.useRef<HTMLParagraphElement>(null);
  const labelRef = React.useRef<HTMLParagraphElement>(null);

  React.useEffect(() => {
    function handleWheel(e: WheelEvent) {
      if (labelRef.current) moveSlowly(e.deltaY, 0, labelRef.current);
      if (titleRef.current) moveSlowly(e.deltaY, 0, titleRef.current);
    }
    const container = document.getElementById('container');
    if (container) {
      container.addEventListener('wheel', handleWheel);
      return () => {
        container.removeEventListener('wheel', handleWheel);
      };
    }
  }, []);

  return (
    <Container id='container' className='flex-container'>
      <div className='flex-item-1'>
        <p id='title' className='title' ref={titleRef}>
          Lost track of time
        </p>
        <div className='image-box image-3'>
          <img
            className='images first-left-image'
            src='https://www.dropbox.com/s/rg4v4rbzn3li6w1/Time%2001.jpg?dl=1'
            alt='Time-01'
            width={600}
            height={800}
          />
          <img
            className='images hover first-left-image'
            src='https://www.dropbox.com/s/15lthdf2l0025do/Time%2001_hover.jpg?dl=1'
            alt='Time-01'
            width={600}
            height={800}
          />
        </div>
        <div className='image-box image-1'>
          <img
            className='images'
            src='https://www.dropbox.com/s/v4avtjscaz1n7ky/Time%2003.jpg?dl=1'
            alt='Time-03'
            width={600}
            height={800}
          />
          <img
            className='images hover'
            src='https://www.dropbox.com/s/up7t6bdsaovyq0o/Time%2003_hover.jpg?dl=1'
            alt='Time-03'
            width={600}
            height={800}
          />
        </div>
        {createImage({
          position: 'right',
          src: 'https://www.dropbox.com/s/kk6680qzlf66hsv/Time%2005.jpg?dl=1',
          alt: 'Time-05',
          direction: 'horizontal',
        })}
        {createHoveredImage({
          position: 'left',
          hoveredSrc:
            'https://www.dropbox.com/s/6sy8s9kycpefs7y/Time%2007_hover.jpg?dl=1',
          src: 'https://www.dropbox.com/s/q8ahtcs459vaw00/Time%2007.jpg?dl=1',
          alt: 'Time-07',
        })}
      </div>
      <div className='flex-item-2'>
        <p id='label' className='label' ref={labelRef}>
          &emsp;If you ask yourself ‘What day is it’ more often these days, you
          are not alone. Being stuck at home during the quarantine has distorted
          our sense of time. Without activity distinctions in calendar, the days
          all seem to blend together.
        </p>
        <video
          autoPlay
          loop
          muted
          className='images image-1'
          width={800}
          height={600}
        >
          <source
            src='https://www.dropbox.com/s/uq8mrdh3i533fwp/Time%2002.mp4?dl=1'
            type='video/mp4'
          />
        </video>
        <img
          className='images image-3'
          src='https://www.dropbox.com/s/da4ix7dzeol9xbl/Time%2004.jpg?dl=1'
          alt='Time-04'
          width={600}
          height={800}
        />
        {createHoveredImage({
          src: 'https://www.dropbox.com/s/cshzguabkjm6mm2/Time%2006.jpg?dl=1',
          hoveredSrc:
            'https://www.dropbox.com/s/mw9lcbhtfvzrt55/Time%2006_hover.jpg?dl=1',
          position: 'left',
          alt: 'Time-06',
          direction: 'horizontal',
        })}
        {createImage({
          src: 'https://www.dropbox.com/s/2tjivc26mcsw9ov/Time%2008.png?dl=1',
          alt: 'Time-07',
          position: 'center',
          direction: 'horizontal',
        })}
      </div>
    </Container>
  );
};

export { Time };
