import React from 'react';
import { Disconnected } from 'pages/Disconnected';
import { Time } from 'pages/Time';
import { Link, BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Anxious from 'pages/Anxious';
import Cursor from 'components/Cursor';
import styled from 'styled-components';
import './style.css';

const StyledLink = styled(Link)`
  color: inherit;
  margin: 5px;
  cursor: none;
`;

const Nav = styled.nav`
  padding: 20px;
`;

const Container = styled.div`
  margin: 0 4vw 0 4vw;
`;

const App = () => {
  return (
    <Router>
      <Nav>
        <StyledLink to='/me/disconnected'>Disconnected</StyledLink>
        <StyledLink to='/me/time'>Time</StyledLink>
        <StyledLink to='/me/anxious'>Anxious</StyledLink>
      </Nav>

      <Cursor />

      <Container>
        <Switch>
          <Route path='/me/disconnected'>
            <Disconnected />
          </Route>
          <Route path='/me/time'>
            <Time />
          </Route>
          <Route path='/me/anxious'>
            <Anxious />
          </Route>
        </Switch>
      </Container>
    </Router>
  );
};
export default App;
