import React from 'react';
import styled from 'styled-components';
import { createImage, createHoveredImage } from 'utils/imageUtil';
import { moveSlowly } from './Disconnected';

const Container = styled.div`
  position: relative;
  p {
    margin-block-start: 0;
    margin-block-end: 0;
  }

  .title {
    text-align: left;
    left: 30vw;
    top: 40vh;
    z-index: 2;
  }

  .label {
    top: 0vh;
  }

  .flex-item-2 > .image-box > .first-right {
    margin-top: 0;
  }
`;

const Anxious = () => {
  const titleRef = React.useRef<HTMLParagraphElement>(null);
  const labelRef = React.useRef<HTMLParagraphElement>(null);

  React.useEffect(() => {
    function handleWheel(e: WheelEvent) {
      if (labelRef.current)
        moveSlowly(e.deltaY, 0, labelRef.current, {
          minRange: 0,
          maxRange: 20,
        });
      if (titleRef.current)
        moveSlowly(e.deltaY, 40, titleRef.current, {
          minRange: 0,
          maxRange: 20,
        });
    }
    const container = document.getElementById('container');
    if (container) {
      container.addEventListener('wheel', handleWheel);
      return () => {
        container.removeEventListener('wheel', handleWheel);
      };
    }
  }, []);

  return (
    <Container id='container' className='flex-container'>
      <div className='flex-item-1'>
        <p className='title' ref={titleRef}>
          Anxious
        </p>
        <p className='label' ref={labelRef}>
          &emsp;Uncertainty is all around us, never more so than today. It
          leaves us feeling anxious, stressed, and powerless whether when you
          are out for grocery shopping or thinking over the direction of your
          life. Anxiety can drain us emotionally and trap us in a spiral of
          endless '“what-ifs”.
        </p>

        {createHoveredImage({
          src:
            'https://www.dropbox.com/s/tdcxjy01p1bsp1g/Anxious%2002.jpg?dl=1',
          hoveredSrc:
            'https://www.dropbox.com/s/1q5qii8ugbg9ab9/Anxious%2002_hover.jpg?dl=1',
          position: 'center',
          alt: 'Anxious-02',
          direction: 'vertical',
        })}
        {createImage({
          src:
            'https://www.dropbox.com/s/wp7p7gijmmud334/Anxious%2004.jpg?dl=1',
          position: 'left',
          alt: 'Anxious-04',
          direction: 'horizontal',
        })}
        {createImage({
          position: 'right',
          src:
            'https://www.dropbox.com/s/ur9gfe11a7u8kjq/Anxious%2006.png?dl=1',
          alt: 'Anxious-06',
          direction: 'horizontal',
        })}
        {createImage({
          position: 'center',
          src:
            'https://www.dropbox.com/s/vkyqigtw0awuae6/Anxious%2008.png?dl=1',
          alt: 'Anxious-08',
          direction: 'horizontal',
        })}
      </div>
      <div className='flex-item-2'>
        {createHoveredImage({
          src:
            'https://www.dropbox.com/s/b8g2a5xl6wqcebu/Anxious%2001.jpg?dl=1',
          hoveredSrc:
            'https://www.dropbox.com/s/4akwl2ry7fiwffe/Anxious%2001_hover.jpg?dl=1',
          position: 'center',
          alt: 'Anxious-01',
          direction: 'vertical',
          className: 'first-right',
        })}
        <video
          autoPlay
          loop
          muted
          className='images image-1'
          width={800}
          height={600}
        >
          <source
            src='https://www.dropbox.com/s/uq8mrdh3i533fwp/Time%2002.mp4?dl=1'
            type='video/mp4'
          />
        </video>
        {createHoveredImage({
          src:
            'https://www.dropbox.com/s/zfo6pbsg3t0qriu/Anxious%2005.jpg?dl=1',
          hoveredSrc:
            'https://www.dropbox.com/s/lvc5z8cg3yenuev/Anxious%2005_hover.jpg?dl=1',
          position: 'center',
          alt: 'Anxious-05',
          direction: 'vertical',
        })}
        {createImage({
          src:
            'https://www.dropbox.com/s/hdfll38p4nhxzv4/Anxious%2007.png?dl=1',
          alt: 'Anxious-07',
          position: 'right',
          direction: 'horizontal',
        })}
      </div>
    </Container>
  );
};

export default Anxious;
